import { useState } from "react";
import Header from "../components/Header";
import apis from "../apis/Apis";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import { useNavigate } from "react-router-dom";
import Post from "../components/Post";
import Select from "@mui/material/Select";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import MenuItem from "@mui/material/MenuItem";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";

import { VerticalAlignBottom } from "@mui/icons-material";

import SearchRoundedIcon from '@mui/icons-material/SearchRounded';

export default function SearchPage() {
  const navigate = useNavigate();
  const [search, setSearch] = useState({ title: "", category: "" });
  const [searchList, setSearchList] = useState([]);
  const onSearch = (e) => {
    setSearch({ ...search, title: e.target.value });
  };

  const onCategory = (e) => {
    setSearch({ ...search, category: e.target.value });
  };
  console.log(search);
  const Click = () => {
    apis
      .searchList(search)
      .then((response) => {
        setSearchList(response.data.data.content);
        console.log(response);
      })

      .catch((error) => {
        console.log(error);
      });
  };

  return (
    <>
      <Header>
        <ArrowBackIcon
          style={{ fontSize: "25px" }}
          onClick={() => {
            navigate("/");
          }}
        ></ArrowBackIcon>
      </Header>
      <div style={{ padding: '5px' }}>
        <div style={{ marginTop: "18%" }}></div>
        <FormControl variant="standard" sx={{ width: "40%" }}>
          <Select
            labelId="demo-simple-select-standard-label"
            id="demo-simple-select-standard"
            label=""
            value={search.category}
            onChange={onCategory}
            displayEmpty
          >
            <MenuItem value="">----</MenuItem>
            <MenuItem value="DEVICE"></MenuItem>
            <MenuItem value="APPLANCE"></MenuItem>
            <MenuItem value="KITCHEN">/</MenuItem>
            <MenuItem value="WOMEN">/</MenuItem>
            <MenuItem value="MEN">/</MenuItem>
            <MenuItem value="BEAUTY">/</MenuItem>
            <MenuItem value="GAME">/</MenuItem>
            <MenuItem value="BOOK"></MenuItem>
            <MenuItem value="TICKET"></MenuItem>
          </Select>
        </FormControl>
        <Button
          variant="contained"
          onClick={Click}
          style={{ marginLeft: "130px" }}
        >
          
        </Button>
        <TextField
          fullWidth
          id="outlined-basic"
          placeholder=" "
          variant="outlined"
          onChange={onSearch}
        // style={{ width: "40%" }}
        />
        {/* <input type="text" onChange={onSearch} /> */}
      </div>


      <div>
        {(searchList[0] === undefined) &&
          <div style={emptyCss}>
            <SearchRoundedIcon sx={{ width: '150px', height: '150px', color: 'gray' }} />
            <p>  : )</p>
          </div>
        }
        {searchList.map((post, idx) => {
          return <Post post={post} key={idx}></Post>;
        })}
      </div>
    </>
  );
}

const emptyCss = {
  height: '80vh',
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  alignItems: 'center',
}